import { Component, Input } from '@angular/core';
import { LocalStorageService } from 'ng2-webstorage';

@Component({

    selector: 'systemAdmins-basicData',
    templateUrl: 'systemAdmins-basicData.component.html'
})

export class SystemAdminsBasicDataComponent {
    public constructor(
        public storage: LocalStorageService
    ) { }
    // default selected object
    objectType: string = 'Cis.Core.Country';
    title: string = 'Countries';
    selected: number = 1;
    isRegion: boolean | undefined;
    isCity: boolean | undefined;
    isLocation: boolean | undefined;
    isOther: boolean = true;
    isDrugUnit: boolean | undefined;
    isDrugFrequency: boolean | undefined;
    selectDemoghraphicTab: boolean = true;
    showProgress!: boolean;
    selectedObject: any;
    setObjectType(event: any) {
        let value = this.selectedObject;
        this.objectType = 'Cis.Core.' + value;
        // this.selected = tabNo;
        //this.title = title;
        this.getObjectTitle(value);

        if (value == "Region") {
            this.isRegion = true;
            this.isOther = false;
            this.isCity = false;
            this.isLocation = false;
            this.isDrugUnit = false;
            this.isDrugFrequency = false;
        } else if (value == "City") {
            this.isCity = true;
            this.isLocation = false;
            this.isOther = false;
            this.isRegion = false;
            this.isDrugUnit = false;
            this.isDrugFrequency = false;
        }
        else if (value == 'DrugUnit') {
            this.isOther = false;
            this.isRegion = false;
            this.isCity = false;
            this.isLocation = false;
            this.isDrugUnit = true;
            this.isDrugFrequency = false;
        }
        else if (value == 'DrugFrequency') {
            this.isOther = false;
            this.isRegion = false;
            this.isCity = false;
            this.isLocation = false;
            this.isDrugUnit = false;
            this.isDrugFrequency = true;
        }
        else if (value == "Location") {
            this.isCity = false;
            this.isLocation = true;
            this.isOther = false;
            this.isRegion = false;
            this.isDrugUnit = false;
            this.isDrugFrequency = false;
        }
        else {
            this.isOther = true;
            this.isRegion = false;
            this.isCity = false;
            this.isLocation = false;
            this.isDrugUnit = false;
            this.isDrugFrequency = false;
        }

    }

    getObjectTitle(selectedValue: any) {
        var selectedObject = this.objectsList.find(obj => obj.value == selectedValue);

        this.title = selectedObject.name;
    }

    objectsList: any[] = [
        { 'value': 'Country', 'name': '', 'nameTranslation': 'Countries' },
        { 'value': 'Region', 'name': '', 'nameTranslation': 'Regions' },
        { 'value': 'City', 'name': '', 'nameTranslation': 'Cities' },
        { 'value': 'Location', 'name': '', 'nameTranslation': 'Location' },
        { 'value': 'Nationality', 'name': '', 'nameTranslation': 'Nationalities' },
        { 'value': 'Religion', 'name': '', 'nameTranslation': 'Religions' },
        { 'value': 'MaritalStatus', 'name': '  ', 'nameTranslation': 'Marital Status' },
        { 'value': 'AttachmentType', 'name': ' ', 'nameTranslation': 'Attachment Type' },
        { 'value': 'VendorCategory', 'name': ' ', 'nameTranslation': 'Vendor Category' },
        { 'value': 'PhysicalResourceType', 'name': '   ', 'nameTranslation': 'Physical Resources Type' },
        { 'value': 'HumanResourceType', 'name': '  ', 'nameTranslation': 'Human Resource Types' },
        { 'value': 'EducationLevel', 'name': ' ', 'nameTranslation': 'Education Level' },
        { 'value': 'VolumeUnit', 'name': ' ', 'nameTranslation': 'Volume Unit' },
        { 'value': 'HeightUnit', 'name': ' ', 'nameTranslation': 'Height Unit' },
        { 'value': 'WeightUnit', 'name': ' ', 'nameTranslation': 'Weight Unit' },
        { 'value': 'DrugUnit', 'name': ' ', 'nameTranslation': 'Drug Unit' },
        { 'value': 'DrugFrequency', 'name': '  ', 'nameTranslation': 'Drug Frequency' },
        { 'value': 'IDType', 'name': ' ', 'nameTranslation': 'ID Type' },
        { 'value': 'ClinicLevel', 'name': ' ', 'nameTranslation': 'Clinic Levels' }];
}